const DATATIP_TEST_TIMEOUT = 3000;
const DATATIP_TEXT = 'datatip_text';
describe('Test MeterBar component', () => {
  it('render', async function () {
    const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
    const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
    const env: Partial<RootEnvironment> = {
      translations: { '@oracle/oraclejet-preact': translations }
    };
    const content = render(
      <RootEnvironmentProvider environment={env}>
        <MeterBar value={20} accessibleLabel="ariaText" />
      </RootEnvironmentProvider>
    );
    const initials = await content.getAllByLabelText('ariaText');
    expect(initials).not.null;
  });

  it('applies Linaria CSS classes', async () => {
    const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
    const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
    const env: Partial<RootEnvironment> = {
      translations: { '@oracle/oraclejet-preact': translations }
    };
    const content = render(
      <RootEnvironmentProvider environment={env}>
        <MeterBar value={20} min={0} max={100} />
      </RootEnvironmentProvider>
    );
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(2);
  });
});

describe('Test Meter Bar ARIA props', () => {
  it('Test default aria properties of Meter Bar', async function () {
    const content = render(<MeterBar value={20} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('aria-valuemax')).equals('100');
    expect(firstElement.getAttribute('aria-valuenow')).equals('20');
    expect(firstElement.getAttribute('aria-valuemin')).equals('0');
    expect(firstElement.getAttribute('aria-valuetext')).equals('20 ');
    expect(firstElement.getAttribute('aria-describedby')).not.null;
    expect(firstElement.getAttribute('aria-labelledby')).null;
  });

  it('Test value, role, min and max property of Meter Bar', async function () {
    const content = render(<MeterBar value={100} max={150} min={50} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('role')).equals('slider');
    expect(firstElement.getAttribute('aria-valuemax')).equals('150');
    expect(firstElement.getAttribute('aria-valuenow')).equals('100');
    expect(firstElement.getAttribute('aria-valuemin')).equals('50');
    expect(firstElement.getAttribute('aria-valuetext')).equals('100 ');
  });

  it('Test accessiblelabel,describedBy,labelledby property of Meter Bar', async function () {
    const testAccessibleLabel = 'TEST-ACCESSIBLE-LABEL';
    const testAriaDescribedBy = 'TEST ARIA-DESCRIBED-BY';
    const testAriaLabeledBy = 'TEST ARIA-LABELLED-BY';
    const content = render(
      <MeterBar
        value={20}
        accessibleLabel={testAccessibleLabel}
        ariaDescribedBy={testAriaDescribedBy}
        ariaLabelledBy={testAriaLabeledBy}
      />
    );
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('aria-label')).equals(testAccessibleLabel);
    const meterDescribedBy = firstElement.getAttribute('aria-describedby');
    const describedByIds = meterDescribedBy?.split(' ');
    expect(describedByIds?.length).equals(3); // including aria describedby for default datatip
    expect(describedByIds ? describedByIds[0] : '').equals('TEST');
    expect(describedByIds ? describedByIds[1] : '').equals('ARIA-DESCRIBED-BY');
    expect(firstElement.getAttribute('aria-labelledby')).equals(testAriaLabeledBy);
  });
});

