describe('Keyboard Interaction', () => {
  const clientHints = getClientHints();
  if (typeof jest === 'undefined' && clientHints.browser === 'chrome') {
    mocha.timeout(DATATIP_TEST_TIMEOUT);
    it('check default datatip correctly triggered with keyboard interaction when value is not set', async () => {
      render(
        <RootEnvironmentProvider>
          <MeterBar />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      await waitFor(() => checkDatatipText('0'), {
        timeout: DATATIP_TEST_TIMEOUT
      });
    });

    it(
      'check datatip correctly triggered with keyboard interaction',
      async () => {
        render(
          <RootEnvironmentProvider>
            <MeterBar value={30} datatip={DATATIP_TEXT} />
          </RootEnvironmentProvider>
        );
        userEvent.tab();
        await waitFor(() => checkDatatipText(DATATIP_TEXT), {
          timeout: DATATIP_TEST_TIMEOUT
        });
      },
      DATATIP_TEST_TIMEOUT
    );

    it('check onInput event is correctly triggered with keyboard interaction', async () => {
      const calledListenerObj = { called: false };
      const handleOnInput = (detail: any) => {
        if (detail.value != null) calledListenerObj.called = true;
      };
      render(
        <RootEnvironmentProvider>
          <MeterBar value={20} onInput={handleOnInput} />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      await waitFor(() => expect(calledListenerObj.called).equals(true));
    });

    it('check onInput is correctly triggered with HOME key by keyboard interaction ', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[HOME]');
      await waitFor(() => expect(inputValue).equals(0));
    });

    it('check onInput is correctly triggered with END key by keyboard interaction ', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[END]');
      await waitFor(() => expect(inputValue).equals(100));
    });

    it('check onCommit event is correctly triggered with keyboard interaction', async () => {
      const calledListenerObj = { called: false };
      const handleOnCommit = (detail: { value: number | null }) => {
        if (detail.value != null) calledListenerObj.called = true;
      };
      render(
        <RootEnvironmentProvider>
          <MeterBar value={20} onCommit={handleOnCommit} />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[ENTER]');
      await waitFor(() => expect(calledListenerObj.called).equals(true));
    });

    it('check step is correctly triggered using down arrow key with keyboard interaction', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            step={0.5}
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[ARROWDOWN]');
      await waitFor(() => expect(inputValue).equals(1.5));
    });

    it('check step is correctly triggered using right arrow key with keyboard interaction', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            step={0.5}
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[ARROWRIGHT]');
      await waitFor(() => expect(inputValue).equals(2.5));
    });
    it('check step is correctly triggered using up arrow key with keyboard interaction', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            step={0.5}
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[ARROWUP]');
      await waitFor(() => expect(inputValue).equals(2.5));
    });
    it('check step is correctly triggered using left arrow key with keyboard interaction', async () => {
      let inputValue: number | undefined = 2;
      render(
        <RootEnvironmentProvider>
          <MeterBar
            step={0.5}
            value={inputValue}
            onInput={(detail) => {
              inputValue = detail.value;
            }}
          />
        </RootEnvironmentProvider>
      );
      userEvent.tab();
      userEvent.keyboard('[ARROWLEFT]');
      await waitFor(() => expect(inputValue).equals(1.5));
    });
  }
});

