const checkDatatipText = (content: string) => {
  const targetElements = document.querySelectorAll('[role="tooltip"]');
  const targetElementItem = targetElements.item(0) as HTMLElement;
  const datatipContainer = targetElementItem?.firstElementChild as HTMLElement;
  expect(datatipContainer?.style.opacity).not.equals('');
  expect(datatipContainer?.style.opacity).not.equals('0');
  expect(datatipContainer?.style.maxHeight).not.equals('');
  expect(datatipContainer?.style.maxHeight).not.equals('0');
  const innerHTML = datatipContainer?.firstElementChild?.innerHTML;
  expect(innerHTML).equals(content);
};
